import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  DatePicker,
  Input
  // Checkbox
} from 'antd'
import moment from 'moment'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxMultipleSelect} from 'Form'
import {API_PRODUCTS_KEYVALUES, API_PRODUCT_CATEGORYSONS, API_MERCHANTPRIORITYCHOICE_KEYVALUES, API_USER_WAREHOUSELIST, API_SHIPPEDORDERS_BUSINESSKEYVALUES} from 'api'
const {RangePicker} = DatePicker

@connect(
  state => ({...state.outDifferentGather, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 重置
  reset = () => {
    const {update, userLoation} = this.props
    update({searchParams: {warehouseCode: {value: userLoation.value, children: userLoation.children}}})
  }

  render() {
    const {
      searchParams: {
        warehouseCode,
        bigCategoryCode,
        centreCategoryCode,
        smallCategoryCode,
        initDate,
        shopType,
        businessType,
        orderCode,
        customerCode,
        shopCode
      },
      userLoation
    } = this.props

    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={8}>
            <CsxSelect
              dateApi={API_USER_WAREHOUSELIST}
              label='查询地点'
              selectItem={{data: warehouseCode ? warehouseCode : {value: userLoation.value, children: userLoation.children}, key: 'warehouseCode', valueKey: ['warehouseCode', 'warehouseName']}}
              onChangeSelect={(value) => {
                this.onInputChange({warehouseCode: value, shopType: ''})
              }}
            />
          </Col>
          <Col span={16}>
            <Row type="flex">
              <CsxMultipleSelect
                dateApi={API_PRODUCTS_KEYVALUES}
                label='商品分类'
                placeholder='大类'
                searchParams={{type: '2'}}
                selectItem={{data: bigCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({bigCategoryCode: value})
                }}
                style={{width: 163}}
              />
              <CsxMultipleSelect
                placeholder='中类'
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{parCode: bigCategoryCode && bigCategoryCode.value, level: '3'}}
                selectItem={{data: centreCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({centreCategoryCode: value})
                }}
                noReady
                errMessage='请先选择大类'
                style={{marginLeft: 10, width: 163}}
              />
              <CsxMultipleSelect
                placeholder='小类'
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{parCode: centreCategoryCode && centreCategoryCode.value, level: '4'}}
                selectItem={{data: smallCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({smallCategoryCode: value})
                }}
                noReady
                errMessage='请先选择中类'
                style={{marginLeft: 10, width: 163}}
              />
            </Row>
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_MERCHANTPRIORITYCHOICE_KEYVALUES}
              label='门店类型'
              searchParams={{warehouseCode: warehouseCode ? warehouseCode.value : userLoation.value}}
              selectItem={{data: shopType, key: 'merchantType', valueKey: 'merchantType'}}
              noReady
              onChangeSelect={(value) => {
                this.onInputChange({shopType: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              data={[{key: '05', value: '退供出库'}, {key: '06', value: '门店配送出库'}, {key: '07', value: '寄售配送出库'}, {key: '08', value: '仓间调拨出库'}, {key: '15', value: '返配出库'}, {key: '18', value: '客户配送'}, {key: '24', value: '商超销售直送'}, {key: '25', value: '商超销售直通'}, {key: '55', value: '商超销售电商'}, {key: '56', value: '无原单退供出库'}]}
              label='业务类型'
              selectItem={{data: businessType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({businessType: value})
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>订单号：</label>
              <div className='box'>
                <Input placeholder="批量查询编码用,号隔开" value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>客户：</label>
              <div className='box'>
                <Input placeholder="批量查询编码用,号隔开" value={customerCode} onChange={(e) => {
                  this.onInputChange({customerCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>门店编号：</label>
              <div className='box'>
                <Input placeholder="批量查询编码用,号隔开" value={shopCode} onChange={(e) => {
                  this.onInputChange({shopCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={16}>
            <div className='commonInput'>
              <label>出库日期:</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker
                  style={{width: '500px'}}
                  ranges={{当前时间: [moment('00:00:00', 'HH:mm:ss'), moment()]}}
                  showTime={{
                    defaultValue: [moment('00:00:00', 'HH:mm:ss'), moment('23:59:59', 'HH:mm:ss')]
                  }}
                  onChange={(mo, string) => {
                    this.onInputChange({initDate: mo})
                  }}
                  format={'YYYY-MM-DD HH:mm:ss'}
                  value={initDate}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



